.advantages {
  width: 100%;
  padding-top: 120px;
  padding-bottom: 120px;
}

.advantages__list {
  width: 100%;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  list-style-type: none;
}

.advantages__item {
  min-width: 447px;
  max-width: 595px;
  width: calc((100% - 30px)/2);
  margin-bottom: 30px;
  padding: 40px 45px 35px 40px;
  border-left: 5px solid $color-deep-sky-blue;
  box-shadow: inset 0 0 0 1px $color-whisper;
}

.advantages__item:nth-child(odd) {
  margin-right: 30px;
}

.advantages__item svg {
  fill: $color-deep-sky-blue;
}

.advantages__item:hover {
  padding-left: 45px;
  color: $color-alice-blue;
  background-color: $color-deep-sky-blue;
  border-left: none;
  box-shadow: none;
}

.advantages__item:hover svg {
  fill: $color-alice-blue;
}

@media (max-width: ($vp-1024 - 1px)) {
  .advantages {
    padding-top: 80px;
    padding-bottom: 80px;
  }

  .advantages__item {
    min-width: 320px;
    max-width: 461px;
  }
}
