  color: #ffffff;
}

.production-services__item:not(:last-child) {
  margin-right: 85px;
}

.production-services__item {
  width: calc((100% - 170px) / 3);
  display: flex;
  position: relative;
}

.production-services__item:not(:last-child)::after {
  width: 85px;
  height: 650px;
  top: 0;
  right: -85px;
  background-color: #041827;
  content: "";
  position: absolute;
}

.production-services__item-link {
  width: 100%;
  height: 100%;
}

.production-services__item-button {
  width: 60px;
  height: 60px;
  right: 0;
  bottom: 0;
  display: flex;
  background-color: #00B2ff;
  position: absolute;
  border-width: 0;
  border-radius: 3px 0 0 0;
  cursor: pointer;
  z-index: 3;
}

.production-services__item-button::after {
  width: 6px;
  height: 6px;
  top: 50%;
  left: 50%;
  border-top: 1px solid #ffffff;
  border-right: 1px solid #ffffff;
  transform: translateY(-50%) rotate(45deg);
  content: "";
  position: absolute;
  z-index: 5;
}

@media (max-width: 1023px) {
  .production-services {
    padding-top: 60px;
    padding-bottom: 75px;
  }
  .production-services__list {
    flex-direction: column;
  }
  .production-services__item {
    width: 100%;
    height: 250px;
  }
  .production-services__item:not(:last-child)::after {
    width: 100%;
    height: 30px;
    top: 100%;
    right: 0;
  }
}
.feedback {
  padding-top: 170px;
  padding-bottom: 250px;
  background: url("../../../img/background/paper_airplane_desktop@1x.png") 20% 50%/510px 285px no-repeat, url("../../../img/svg/polygon-background.svg") top left/contain no-repeat, linear-gradient(to top, #00B3ff, #00B3ff) center/cover no-repeat;
}

.feedback__inner-wrap {
  width: 580px;
  margin: 0 0 0 auto;
}

.feedback__title {
  margin: 0;
  margin-bottom: 15px;
  padding: 0;
  font-size: 36px;
  font-weight: 400;
  line-height: 51px;
  color: #ffffff;
}

.feedback__promo-text {
  margin-bottom: 35px;
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  color: #ffffff;
}

@media (max-width: 1023px) {
  .feedback {
    background-image: none;
    background-color: #00B3ff;
  }
  .feedback__inner-wrap {
    width: 100%;
    margin: 0 auto;
  }
}
.form {
  display: flex;
  flex-wrap: wrap;
}

.form input {
  width: 275px;
  height: 50px;
  margin-bottom: 20px;
  padding: 15px;
  box-sizing: border-box;
  font-family: "PT Mono", monospace, "Courier New";
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  color: #ffffff;
  background-color: rgba(4, 20, 39, 0.15);
  border-width: 0;
  border-radius: 3px;
}

.form input::placeholder,
.form textarea::placeholder {
  font-family: "PT Mono", monospace, "Courier New";
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  color: #ffffff;
}

.form__name-field {
  margin-right: 30px;
}

.form button {
  margin-right: 50px;
  padding: 15px 50px;
  font-family: "Roboto Condensed", sans-serif, Arial;
  font-size: 16px;
  font-weight: 700;
  line-height: 19px;
  color: #ffffff;
  text-transform: uppercase;
  background-color: #041427;
  border-width: 0;
  border-radius: 3px;
  order: 1;
}

.form__question-textarea {
  margin-bottom: 35px;
}

.form textarea {
  width: 580px;
  min-height: 155px;
  padding: 25px 15px;
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  color: #ffffff;
  background-color: rgba(4, 20, 39, 0.15);
  border-width: 0;
  border-radius: 3px;
}

.form__consent-checkbox {
  width: 260px;
  padding-top: 15px;
  order: 2;
  position: relative;
}

.form__consent-checkbox input {
  display: none;
  margin: 0;
}

.form__consent-checkbox b {
  margin-left: 25px;
  display: block;
  font-size: 14px;
  font-weight: 400;
  line-height: 16px;
  color: #ffffff;
  text-align: left;
}

.form__consent-checkbox span {
  width: 15px;
  height: 15px;
  top: 22px;
  left: 0;
  border: 1px solid #ffffff;
  border-radius: 2px;
  content: "";
  position: absolute;
  z-index: 3;
}

.form__consent-checkbox input:checked + span {
  background-image: url("../../../img/svg/tick.svg");
  background-repeat: no-repeat;
  background-position: 50% 50%;
}

@media (max-width: 1023px) {
  .form__name-field {
    width: calc((100% - 30px) / 2);
  }
  .form__telephone-field {
    width: calc((100% - 30px) / 2);
  }
  .form input {
    width: 100%;
    min-width: 320px;
  }
  .form__question-textarea {
    width: 100%;
  }
  .form textarea {
    width: 100%;
    min-width: 670px;
  }
}
.title {
  font-family: "Roboto Condensed", sans-serif, Arial;
  text-align: left;
}

.title--footer {
  font-size: 15px;
  font-weight: 400;
  line-height: 16px;
  text-transform: uppercase;
}

.button {
  display: inline-block;
  font-family: "Roboto Condensed", sans-serif, Arial;
  text-align: center;
  text-transform: uppercase;
  border-radius: 3px;
}

.toggle-button {
  display: none;
}

@media (max-width: 767px) {
  .toggle-button {
    width: 15px;
    height: 15px;
    display: block;
    background-color: transparent;
    border-width: 0;
    cursor: pointer;
  }
  .toggle-button--closed {
    background: url("../../../img/svg/minus.svg") center/contain no-repeat;
  }
  .toggle-button--opened {
    background: url("../../../img/svg/plus.svg") center/contain no-repeat;
  }
}
